/* Scroll Down Button */
class ScrollDownBtn extends React.PureComponent {

    scrollDown = (event, ref) => {
        //console.log(this.refs, ref)
        const offset = 33;
        const el = ref.current;
        const adjustedOffset = el.offsetTop - offset
        window.scrollTo({ top: adjustedOffset, behavior: 'smooth' })
        //this.scrollToExtend(adjustedOffset, 100)
    }

    render() {
        return (
            <span className="scrollDown" onClick={e => this.scrollDown(e, this.props.el)} 
            
            data-aos={
                (this.props.currentBreakpoint <= 769)
                ? null
                : "flip-down"
            } data-aos-delay="1500"    
                data-174={this.props.currentBreakpoint <= 769 ? "transition:;" : null}
                data-175={this.props.currentBreakpoint <= 769 ? "opacity:1;transition:none;" : null}
                data-349={this.props.currentBreakpoint <= 769 ? null : "transition:;" }
                data-350={this.props.currentBreakpoint <= 769 ? null : "opacity:1;transition:none;"}
                
                data-315={this.props.currentBreakpoint <= 769 ? "opacity:0;transition:none;" : null}
                data-550={this.props.currentBreakpoint <= 769 ? null : "opacity:0;transition:none;"}
            >
                <img src={Images.downArrow} alt="scroll down" />
                <img className="hover" src={Images.downArrowHover} alt="scroll down" />
            </span>
        )
    }
}


/* Banner & Bg Elements */
class Banner extends React.Component {
    
    state = { offset: 0 }

    componentDidMount() {
        window.addEventListener('scroll', this.parallaxShift);
    }
    componentWillUnmount() {
        window.removeEventListener('scroll', this.parallaxShift);
    }
    parallaxShift = () => {
        this.setState({
            offset: document.documentElement.scrollTop
        });
    };

    render() {

        let isCompact = false;
        if (this.props.isCompact === undefined) {
            isCompact = false
        }
        else {
            isCompact = this.props.isCompact;
        }

        let scrollTop = this.props.scrollTop;

        return (
            <> 
            {
                (isCompact) 
                ? (
                    <div className="banner">
                        <div className="bpart1" style={{ backgroundImage: "url(" + Images.bannerPart1 + ")" }}></div>
                        <div className="bg"></div>
                    </div>
                )
                : (
                    (this.props.currentBreakpoint <= 769)
                    ? (
                        <div className="banner">
                            {/* <div className="bpart1"
                                style={{ backgroundImage: 
                                        (this.props.currentBreakpoint <= 480) 
                                            ? "url(" + Images.mbannerPart1 + ")" 
                                            : "url(" + Images.bannerPart1 + ")" 
                                }}></div> */}
                            {/* <div className="bpart2"
                                style={{ backgroundImage:  
                                        (this.props.currentBreakpoint <= 480) 
                                            ? "url(" + Images.mbannerPart2 + ")" 
                                            : "url(" + Images.bannerPart2 + ")", backgroundPositionY: this.state.offset / 15 
                                }}></div> */}
                            <div className="bpart3"
                                style={{ 
                                    backgroundImage: 
                                        (this.props.currentBreakpoint <= 480) 
                                            ? "url(" + Images.mbannerPart3 + ")" 
                                            : "url(" + Images.bannerPart3 + ")", backgroundPositionY: this.state.offset / 8 
                                }}></div>
                            {/* <div className="bpart4"
                                style={{ backgroundImage: 
                                        (this.props.currentBreakpoint <= 480) 
                                            ? "url(" + Images.mbannerPart4 + ")" 
                                            : "url(" + Images.bannerPart4 + ")", backgroundPositionY: this.state.offset / 10
                                }}></div> */}
                            {/* <div className="bpart5"
                                style={{ backgroundImage: 
                                        (this.props.currentBreakpoint <= 480) 
                                            ? "url(" + Images.mbannerPart5 + ")" 
                                            : "url(" + Images.bannerPart5 + ")", backgroundPositionY: this.state.offset / 5 
                                }}></div> */}
                            <div className="bpart6"
                                style={{
                                    backgroundImage:
                                        (this.props.currentBreakpoint <= 480)
                                            ? "url(" + Images.mbannerPart6 + ")"
                                            : "url(" + Images.bannerPart6 + ")"
                                }}></div>
                            <div className="bg"></div>
                        </div>
                    )
                    : (
                        <div className="banner">
                            <div className="bpart1"
                                data-aos={scrollTop == null ? "fade-down" : ""}
                                data-aos-duration={scrollTop == null ? "1000" : ""}
                                data-aos-delay={scrollTop == null ? "1750" : ""}
                                style={{ backgroundImage: "url(" + Images.bannerPart1 + ")" }}></div>
                            <div className="bpart2"
                                data-aos={scrollTop == null ? "fade-left" : ""}
                                data-aos-duration={scrollTop == null ? "1000" : ""}
                                data-aos-delay={scrollTop == null ? "1750" : ""}
                                style={{ backgroundImage: "url(" + Images.bannerPart2 + ")", backgroundPositionY: this.state.offset / 15 }}></div>
                            <div className="bpart3"
                                data-aos={scrollTop == null ? "fade-up" : ""}
                                data-aos-duration={scrollTop == null ? "1000" : ""}
                                data-aos-delay={scrollTop == null ? "1750" : ""}
                                style={{ backgroundImage: "url(" + Images.bannerPart3 + ")", backgroundPositionY: this.state.offset / 8 }}></div>
                            <div className="bpart4"
                                data-aos={scrollTop == null ? "zoom-out" : ""}
                                data-aos-duration={scrollTop == null ? "1000" : ""}
                                data-aos-delay={scrollTop == null ? "1750" : ""}
                                style={{ backgroundImage: "url(" + Images.bannerPart4 + ")", backgroundPositionY: this.state.offset / 10 }}></div>
                            <div className="bpart5"
                                data-aos={scrollTop == null ? "zoom-in" : ""}
                                data-aos-duration={scrollTop == null ? "1000" : ""}
                                data-aos-delay={scrollTop == null ? "1750" : ""}
                                style={{ backgroundImage: "url(" + Images.bannerPart5 + ")", backgroundPositionY: this.state.offset / 5 }}></div>
                            <div className="bpart6"
                                data-aos={scrollTop == null ? "fade-right" : ""}
                                data-aos-duration={scrollTop == null ? "1000" : ""}
                                data-aos-delay={scrollTop == null ? "1750" : ""}
                                style={{ backgroundImage: "url(" + Images.bannerPart6 + ")" }}></div>
                            <div className="bg"></div>
                        </div>
                    )
                )
            }
            </>
            

        )
    }
 }


/* BG Profile & Bio */
class Profile extends React.Component {

    shouldComponentUpdate() {
        return false;
    }


    render() {
        return (
            
            <div className="profile" 
                data-0="position:relative;top:50%;pointer-events:all;" 
                data-305="opacity:1;"
                data-400={
                    (this.props.currentBreakpoint <= 769)
                        ? "opacity:0;pointer-events:none;"
                        : null
                }
                data-550="opacity:0;pointer-events:none;"
            >
          
                <h1 data-aos="fade-in">Jeremy Soh</h1>
                <div className="bio">
                    <span className="sub"
                        data-90={"opacity:0;"}
                        data-165={
                            (this.props.currentBreakpoint <= 769) 
                            ? "opacity:1" 
                            : "opacity:0"
                        }
                        data-280={"opacity:1;"}
                    >{6 + Math.floor(Math.abs((new Date() - new Date("2021-06-01")) / 31536000000))} years {SepHighlight("of")} professional experience</span>
                    
                    <div className="bio-inner"
                        data-75={"opacity:1;"}
                        data-150={
                            (this.props.currentBreakpoint <= 769) 
                            ? "opacity:0" 
                            : "opacity:1"
                        }
                        data-225={"opacity:0"}
                    >
                        <span className="role" data-aos="zoom-out" data-aos-delay="300"
                            data-75={
                                (this.props.currentBreakpoint <= 960)
                                ? "letter-spacing:1.5px;"
                                : "3px"
                            }
                            data-150={
                                (this.props.currentBreakpoint <= 960) 
                                ? (
                                    (this.props.currentBreakpoint <= 769) 
                                    ? "letter-spacing:50px" 
                                    : "letter-spacing:1.5px;"
                                )
                                : "letter-spacing:3px"
                            }
                            data-225={"letter-spacing:50px"}
                        >Freelance Software Developer</span>

                        <span className="desc" data-aos="zoom-out" data-aos-delay="900"
                            data-75={
                                (this.props.currentBreakpoint <= 960)
                                ? "letter-spacing:2.5px;"
                                : "4.5px"
                            }
                            data-150={
                                (this.props.currentBreakpoint <= 960)
                                    ? (
                                        (this.props.currentBreakpoint <= 769)
                                            ? "letter-spacing:30px"
                                            : "letter-spacing:2.5px;"
                                    )
                                    : "letter-spacing:4.5px"
                            }
                            data-225={"letter-spacing:30px"}
                        >fullstack webapps {SepHighlight('/')} mobile apps</span>
                    </div>
             
                    
                </div>
                <div className="socialWrap" ref="socialWrap" data-aos="fade-up" data-aos-delay="1200" 

                    data-75={"top:0px;"}
                    data-150={
                        (this.props.currentBreakpoint <= 960) 
                        ? (
                            (this.props.currentBreakpoint <= 769)
                            ?"top:-25px;pointer-events:all;"
                            :"top:0px;"
                        )
                        :"top:0px;"
                    }
                    
                    data-225={
                        (this.props.currentBreakpoint <= 960) ? "top:-25px;pointer-events:all;" : "top:-30px;pointer-events:all;"
                    }
                    data-550={"pointer-events:none;"}        
                >
                    <Suspense fallback={<></>}>
                        <Socials currentBreakpoint={this.props.currentBreakpoint} />
                    </Suspense>
                </div>
            </div>

        )
    }
}


