/* Logo */
class Logo extends React.Component{

    shouldComponentUpdate(){
        return false;
    }

    render(){

        return(

            <>
            {
                (this.props.isCompact === undefined && !this.props.isCompact)
                ? (
                    <div className="logo"
                        data-350={"top:30px;"}
                        data-400={"top:11px;"}
                    >
                        <Link to={{pathname: "/"}}  
                                data-150={"pointer-events:all"}
                                data-225={"pointer-events:none"}>

                            <img src={
                                (this.props.currentBreakpoint <= 960) ? Images.logoNoAliasBig : Images.logo
                            } alt="Logo"
                                data-150={"opacity:1;"}
                                data-225={
                                    (this.props.currentBreakpoint <= 960) 
                                    ? "height:31px;opacity:0;"
                                    : "height:62px;opacity:0;"
                                }
                                data-305={"height:31px;"}
                            />
                        </Link>
                        <Link to={{pathname: "/"}}
                            data={"pointer-events:none;"}
                            data-305={ "pointer-events:all;"} >

                            <img src={Images.logoNoAliasBig} alt="Logo"
                                data={
                                    (this.props.currentBreakpoint <= 960) 
                                        ? "height:31px;left: -500px;position: absolute;top:0;bottom:0;"
                                        :"height:40px;left: -500px;position: absolute;top:0;bottom:0;"
                                }
                                data-305={"opacity:1;"}
                                data-475={
                                    (this.props.currentBreakpoint <= 769) 
                                    ? "opacity:1;left:0px;"
                                    : null
                                }
                                data-684={"opacity:1;left:0px;"}
                            />
                        </Link>
                   
                     
                    </div>
                )
                :(
                    <div className="logo">
                        <Link to={{pathname: "/"}} >
                            <img src={Images.logoNoAliasBig} alt="Logo" />
                        </Link>
                    </div>
                )
                
            }
            </>
           )
    }
}

/* TopDiv */
export class TopDiv extends React.PureComponent{

    state={
        isCompact: false
    }

    componentDidMount(){
        this.props.s.refresh();
        if(this.props.isCompact !== undefined){
            this.setState({
                isCompact: this.props.isCompact
            })
        }
        else{
            this.setState({
                isCompact: false
            })
        }
    }

    render(){

        let isCompact = false;
        if (this.props.isCompact === undefined) {
            isCompact = false
        }
        else {
            isCompact = this.props.isCompact;
        }
        
        return(
            <>
            {
                (isCompact)
                    ? (
                        <div id="topDiv" className="compact">
                            <Logo isCompact={this.state.isCompact} currentBreakpoint={this.props.currentBreakpoint} />
                            <Link className="topBarCTA" to={{ pathname: "/" }}>&#10550;<span className="mobile-hide"> Back to </span>Home<span className="tablet-hide">page</span><span className="mobile-hide"></span></Link>
                            <ProfileAfter isCompact={this.state.isCompact} />
                            <BgOverlay />
                            <Banner isCompact={this.state.isCompact}/>
                        </div>
                    )
                    : (
                        <div id="topDiv"
                            data-0={this.props.currentBreakpoint <= 960 
                                ? "height:550px"
                                : "height:750px"
                            }

                            data-450={this.props.currentBreakpoint <= 960 
                                ? "border-bottom:4px solid rgba(255, 97, 127, 0);height:66px;"
                                : "border-bottom:4px solid rgba(255, 97, 127, 0);height:300px;"
                            }

                            data-684={
                                (this.props.currentBreakpoint <= 960) 
                                    ? "border-bottom:4px solid rgba(255, 97, 127, 1);height:56px" 
                                    : "border-bottom:4px solid rgba(255, 97, 127, 1);height:66px"
                            }
                        >
                            <Logo currentBreakpoint={this.props.currentBreakpoint} />
                            <Profile currentBreakpoint={this.props.currentBreakpoint} />
                            <ProfileAfter currentBreakpoint={this.props.currentBreakpoint} />
                            <ScrollDownBtn currentBreakpoint={this.props.currentBreakpoint} el={this.props.el} />
                            <BgOverlay />
                            <Banner currentBreakpoint={this.props.currentBreakpoint}   />
                        </div>
                    )
            }
            </>
            
        )
    }
}